body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#grad {
  background-image: -webkit-linear-gradient(left, rgb(45,83,143) ,rgb(0,162,97));
  background-image: linear-gradient(to right, rgb(45,83,143) ,rgb(0,162,97));
  text-align: center;
  padding: 2rem 0;
  margin-top:1rem;
  color:white;
  font-size:2rem;
  font-weight: 600;
}
#favourite-text {
  color: rgb(0,167,96);
}

